.button-styling {
    background-color: black;
    font-weight: bold;
    color: white;
    height: 50px; /* Adjust height as needed */
    width: 25%; /* Adjust width as needed */
    border: none;
    cursor: pointer;
    box-shadow: 5px 4px 8px rgba(194, 8, 8, 0.1); /* Add shadow effect */
}

// FormComponent.scss

.form-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding:10%;
    width: 50%;
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);

    input{
        margin:7px;
        border-radius: 5px;
    }
  }
  
  .button-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
  
  button {
    margin-left: 10px;
    padding: 8px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .cancel-button {
    color: #007bff; // Blue color
    background-color: transparent;
  }
  
  .ok-button {
    color: white;
    background-color: #007bff; // Blue color
  }
  


  // DropdownMenu.scss

.dropdown-container {
    margin-bottom: 20px;
  }
  
  select {
    width: 25%;
    padding: 8px 16px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: invert;
    margin-bottom: 7px;
  }
  
  option {
    font-size: 16px;
  }
  


  @media only screen and (max-width: 620px) {
    .button-styling{
      font-size: 10px;
    }
  }
