.table-wrapper {
    margin: 5rem;
  }
  
  .product-table {
    width: 100%;
    border-collapse: collapse;
  
    th, td {
      border: 1px solid #ddd;
      padding: 8px;
      text-align: left;
    }
  
    th {
      background-color: #f2f2f2;
    }
  
    td img {
      max-width: 100px;
      height: auto;
    }

}


.container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25%;
}